@import url(https://fonts.googleapis.com/css?family=Roboto:400,900&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
  vertical-align: baseline;
	box-sizing: border-box;
	text-decoration: none;
	color: black;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul{
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
:root {
  --black: #000000;
  --white: #ffffff;
  --dark-green: #295917;
}
body {
  background-color: var(--white);
  font-family: 'Roboto', sans-serif;
}

.display-none {
  display: none;
}

main {
  min-height: 100vh;
}

.isHidden {
  display: none;
}
.main-header {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: center;
  align-items: center;

  padding: 12px 26px;
  background-color: var(--black);
  border-bottom: solid 6px var(--dark-green);
}

.main-header * {
  color: var(--white);
}
.nav-links {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: center;
  align-items: center;
}

.nav-links li {
  padding: 12px;
  font-weight: 900;
}
.main-navigation__title {
  font-size: 24px;
  font-weight: 900;
}
.select-input-cont {
  width: 120px;
  padding: 4px 4px;
  border: solid 1px black;
  border-radius: 16px;
  background-color: white;
}

.select-input {
  width: 85%;
  border: none;
  font-weight: 900;
  cursor: pointer;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}
.page-error {
  max-width: 500px;
  margin: 16px auto;
  text-align: center;
}

.page-error__title {
  font-size: 18px;
  font-weight: 900;
  margin: 12px 0;
}

.page-error__btn-cont {
  display: flex;
  justify-content: center;
}

.page-error__button {
  background-color: var(--dark-green);
  color: white;
  border: solid 2px black;
  margin: 0 12px;
  padding: 4px 8px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
}

.page-error__button-danger {
  background-color: rgb(184, 0, 0);
}
.modal__background {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  position: fixed;
  min-width: 100vw;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.76);
}

.modal {
  background-color: white;
  width: 400px;
  text-align: center;
  border: solid 4px rgb(14, 14, 14);
}

.modal__header {
  background-color: var(--dark-green);
  font-size: 18px;
  font-weight: 900;
  padding: 18px;
}

.modal__header * {
  color: var(--white);
}

.modal__content {
  padding: 18px;
}

.modal__content-text-input-inline-cont * {
  margin: 0 6px
}

.modal__content-text-input-inline-cont input {
  padding: 4px;
  border: solid 2px black;
}

.modal__footer {
  padding: 18px;
}

.modal__footer * {
  margin: 6px;
}

.modal-btn {
  background-color: var(--dark-green);
  color: var(--white);
  border: solid 2px black;
  padding: 4px 8px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
}

.modal__game-infos-text {
  display: block;
  width: 90%;
  margin: auto;
  text-align: center;
  color: red;
}

.modal__game-infos-text p {
  margin-bottom: 12px;
  line-height: 18px;
}

/* Graph */

.modal-graph-cont {
  width: 800px !important;
}

.modal__content .recharts-wrapper {
  margin: auto;
}
.spinnerContLarge {
  width: 100%;
}

.spinnerSmall {
  text-align: center;
  margin: auto;
  width: 30px;
}
/* Global */

.start-game-form {
  background-color: var(--white);
  border: solid 4px black;
  text-align: center;
  width: 500px;
  margin: auto;
  padding: 8px;
}

.start-game-form__section-title {
  display: block;
  font-size: 18px;
  margin-bottom: 8px;
}

.label-radio-btn-selected {
  background-color: var(--dark-green);
  border: solid 2px black;
  color: var(--white);
}

.start-game-form__btn {
  background-color: var(--dark-green);
  color: white;
  border: solid 2px black;
  padding: 4px 8px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
}

.start-game-form__btn:hover {
  background-color: rgb(23, 117, 20);
}

/* Radio button */

.start-game-form__game-type-cont__inputs-radio-cont {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 400px;
  margin: auto;
  margin-bottom: 16px;
  padding: 6px 0;
  border: solid 2px black;
  border-radius: 16px;
}

.start-game-form__game-type-cont__inputs-radio-cont * {
  width: 60px;
  font-size: 14px;
  font-weight: bold;
  border-radius: 16px;
  margin: 0 6px;
  padding: 4px 6px;
  cursor: pointer;
}

/* header */

.start-game-title {
  text-align: center;
  font-size: 28px;
  font-weight: 900;
  margin: 16px 0;
}

/* set leg nbr choice */

.start-game-form__sets-legs-cont {
  margin-bottom: 16px;
}

.start-game-form__sets-legs-cont__item-cont {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  width: 380px;
  margin: auto;
  margin-bottom: 8px;
  padding-left: 50px;
}

.start-game-form__sets-legs-cont__item-cont * {
  display: block;
  margin: 0 8px;
}

/* select player */

.start-game-form__select-player-cont {
  margin: 12px;
}

.start-game-form__select-player-cont__element-cont {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.start-game-form__select-player-cont__element-cont > * {
  margin: 4px 6px;
}

/* New player */

.start-game-form__add-new-player-cont {
  width: 80%;
  margin: auto;
  margin-bottom: 12px;
  padding: 10px 0;
  border: solid 2px black;
}

.btn-add-player {
  background-color: var(--dark-green);
  color: var(--white);
  border: solid 2px black;
  padding: 4px 8px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
}

.btn-add-player:hover {
  background-color: rgb(23, 117, 20);
}

/* error */

.start-game-form__error-cont {
  font-weight: 900;
  width: 80%;
  padding: 8px;
  margin: auto;
  margin-bottom: 12px;
  background-color: rgb(165, 0, 0);
}

.start-game-form__error-cont * {
  color: white;
}

.create-player-msg {
  padding-top: 8px;
  font-size: 16px;
  font-weight: 900;
}
.checkout-cont {
  text-align: center;
  max-width: 90%;
  margin: auto;
  margin-bottom: 6px;
  border: solid 2px black;
}

.checkout-cont__title {
  font-size: 18px;
  margin: 6px 0;
  cursor: pointer;
}


.checkout__list li {
  padding: 6px 0;
}
.game__player-item {
  max-width: 250px;
  padding: 12px 4px;
  border: solid 2px black;
  border-radius: 12px;
  margin-bottom: 8px;
}

.game__player-item__infos {
  margin-bottom: 12px;
}

.game__player-item__name {
  font-size: 18px;
  font-weight: 900;
  text-align: center;
  margin-bottom: 6px;
}

.game__player-item__score {
  font-size: 24px;
  font-weight: 900;
  text-align: center;
}

.game__player-item__stats-table {
  margin: auto;
  width: 60%;
  line-height: 22px;
}

.game__player-item__stats-table th {
  text-align: left;
}

.game__player-item__stats-table td {
  text-align: right;
}

.stats__averages-cont-title-stats-page {
  font-size: 18px;
  font-weight: 900;
  text-align: center;
}

.stats__averages-cont-stats-page {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;

  text-align: center;
  margin: 12px;
  border-bottom: 1px solid black;
}

.stats__averages-cont-title-current-player-stats {
  font-size: 18px;
  font-weight: 900;
  text-align: center;
}

.stats__averages-cont-current-player-stats {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;

  text-align: center;
  margin: 12px;
  border-bottom: 1px solid black;
}

.stats__averages-cont-stats-page > * {
  margin: 2px 8px;
}

.match-number-infos-cont__data {
  margin: 4px 0;
}
.stats-object-cont__title {
  width: 130px;
  font-weight: 900;
  margin-bottom: 8px;
}

.stats-object-cont__data-cont {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: flex-start;
  align-items: flex-start;

  margin:auto;
  width: 130px;
  padding: 8px 0;
  border-bottom: solid black 1px;
}
.game__current-player-stats {
  max-width: 300px;
  text-align: center;
  margin: auto;
  margin-bottom: 12px;
  padding: 12px 4px;
  border: solid 2px black;
  border-radius: 16px;
}

.game__current-player-stats h2 {
  font-size: 18px;
}

.game__current-player-stats > * {
  margin-bottom: 12px;
}
.game__current-player {
  max-width: 300px;
  text-align: center;
  margin: auto;
  margin-bottom: 12px;
  padding: 12px 4px;
  border: solid 2px black;
  border-radius: 16px;
}

.game__current-player-info-cont h2 {
  font-size: 18px;
  margin-bottom: 6px;
}

.game__current-player-info-cont__name {
  font-size: 18px;
  font-weight: 900;
  margin-bottom: 6px;
}

.game__current-player-info-cont__score {
  font-size: 34px;
  font-weight: 900;
  margin-bottom: 6px;
}

.game__current-player__form {
  display: block;
  width: 90%;
  margin: 12px auto;
  border: solid 2px black;
  border-radius: 16px;
}

.game__current-player__form h2 {
  margin: 12px 0;
}

.game__current-player__form__input-cont {
  margin: 6px 0;
}

.game__current-player__form__input-cont label {
  margin-right: 6px;;
}

.delete-cross-dart-input {
  font-size: 18px;
  margin-left: 4px;
  color: rgb(184, 0, 0);
  cursor: pointer;
}

.game__current-player__form button, .game__current-player__undo-btn, .game__new-game-btn, .game__back-home-btn {
  background-color: var(--dark-green);
  color: var(--white);
  border: solid 2px black;
  padding: 4px 8px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  margin-bottom: 12px;
}

.game__back-home-btn {
  color: var(--dark-green);
  background-color: white;
  border-color: var(--dark-green);
}

.game__current-player__undo-btn {
  background-color: rgb(184, 0, 0);
}

.game__current-player__undo-btn i {
  color: white;
  margin-right: 8px;
}

.game__victory-message {
  font-size: 24px;
  font-weight: 900;
  text-align: center;
  line-height: 30px;
  margin-bottom: 12px;
  color: var(--dark-green);
}

.game__throw-validaion-error {
  width: 80%;
  margin: auto;
  background-color: rgb(216, 0, 0);
  color: white;
  font-weight: 900;
  padding: 6px 4px;
  margin-bottom: 6px;
}

.game__ended-btn-cont {
  width: 175px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  margin: auto;
}
#dart-board-cont {
  text-align: center;
}

map {
  cursor: pointer;
  -webkit-user-select: none; /* webkit (safari, chrome) browsers */
  -moz-user-select: none; /* mozilla browsers */ /* webkit (konqueror) browsers */
  -ms-user-select: none; /* IE10+ */ 
  user-select: none;
}

.match-number-infos-cont {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;

  text-align: center;
  margin: 12px;
}

.match-number-infos-cont > * {
  margin: 2px 8px;
}

.match-number-infos-cont__data {
  margin: 4px 0;
}
.stats__stats-cont__player-match-stats {
  min-width: 250px;
  border: solid 2px black;
  border-radius: 12px;
  padding: 12px 0;
  margin: 8px 8px;
}

.statscont__player-name {
  font-size: 24px;
  font-weight: 900;
  text-align: center;
  margin-bottom: 16px;
}

.stats__objects-stats-cont__stats-page {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-content: flex-start;
  align-items: flex-start;

  text-align: center;
  margin-bottom: 150px;
}

.stats__objects-stats-cont__stats-page > * {
  margin: 12px 20px;
}

.stats__objects-stats-cont__player-match-stats {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-content: flex-start;
  align-items: flex-start;
  max-width: 600px;
  margin: auto;
}

.stats__objects-stats-cont__player-match-stats > * {
  margin: 0 10px;
}
.game-container {
  display: grid;
  grid-template-columns: 40% 30% 30%;
}

.game-container > div {
  padding: 25px;
}

.game__stats-cont {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding-bottom: 100px;
}

.game-stats-container h3 {
  font-size: 18px;
  font-weight: 900;
  text-align: center;
  margin: 16px 0;
}
.stats-page-cont {
  max-width: 800px;
  margin: auto;
}

.stats-page-cont__form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 16px 0;
}

.stats-page-cont__form > * {
  margin: 6px;
}

.stats-page-cont__form select {
  width: 100%;
  text-align: center;
}

.btn-stat-player {
  background-color: var(--dark-green);
  color: var(--white);
  border: solid 2px black;
  border-radius: 12px;
  padding: 4px 10px;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
}

.stat-hover:hover {
  cursor: pointer;
}

.stat-hover:hover * {
  color: var(--dark-green)
}
.about {
  text-align: center;
  margin: 1rem 0;
}

.about h2 {
  font-size: 1.5rem;
  margin: 1rem 0;
}

.website-link {
  color: rgb(23, 133, 23);
}
