.game__current-player {
  max-width: 300px;
  text-align: center;
  margin: auto;
  margin-bottom: 12px;
  padding: 12px 4px;
  border: solid 2px black;
  border-radius: 16px;
}

.game__current-player-info-cont h2 {
  font-size: 18px;
  margin-bottom: 6px;
}

.game__current-player-info-cont__name {
  font-size: 18px;
  font-weight: 900;
  margin-bottom: 6px;
}

.game__current-player-info-cont__score {
  font-size: 34px;
  font-weight: 900;
  margin-bottom: 6px;
}

.game__current-player__form {
  display: block;
  width: 90%;
  margin: 12px auto;
  border: solid 2px black;
  border-radius: 16px;
}

.game__current-player__form h2 {
  margin: 12px 0;
}

.game__current-player__form__input-cont {
  margin: 6px 0;
}

.game__current-player__form__input-cont label {
  margin-right: 6px;;
}

.delete-cross-dart-input {
  font-size: 18px;
  margin-left: 4px;
  color: rgb(184, 0, 0);
  cursor: pointer;
}

.game__current-player__form button, .game__current-player__undo-btn, .game__new-game-btn, .game__back-home-btn {
  background-color: var(--dark-green);
  color: var(--white);
  border: solid 2px black;
  padding: 4px 8px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  margin-bottom: 12px;
}

.game__back-home-btn {
  color: var(--dark-green);
  background-color: white;
  border-color: var(--dark-green);
}

.game__current-player__undo-btn {
  background-color: rgb(184, 0, 0);
}

.game__current-player__undo-btn i {
  color: white;
  margin-right: 8px;
}

.game__victory-message {
  font-size: 24px;
  font-weight: 900;
  text-align: center;
  line-height: 30px;
  margin-bottom: 12px;
  color: var(--dark-green);
}

.game__throw-validaion-error {
  width: 80%;
  margin: auto;
  background-color: rgb(216, 0, 0);
  color: white;
  font-weight: 900;
  padding: 6px 4px;
  margin-bottom: 6px;
}

.game__ended-btn-cont {
  width: 175px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  margin: auto;
}